.Settings {
  padding: 1rem;
  font-size: 1.25rem;
  font-weight: bold;
}

.Settings .container {
  border: 0.25rem solid black;
  display: grid;
  grid-template-columns: 5rem minmax(0, 1fr);
  grid-row-gap: 1rem;
  padding: 1rem;
}

.Settings label {
  height: 2rem;
  display: flex;
  align-items: center;
}

.Settings input[type="tel"] {
  width: 100%;
  height: 2rem;
  border: none;
  outline: none;
  font-size: 1.25rem;
}
