.Color {
  list-style-type: none;
  display: flex;
  gap: 0.5rem;
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
}

.Color button {
  width: 2rem;
  height: 2rem;
  background-color: transparent;
  border-width: 1rem;
  border-style: solid;
  border-radius: 50%;
  outline: none;
  display: inline-block;
  transition: 100ms;
}

.Color button:hover, .Color button:focus {
  box-shadow: 0 0 0.5rem black;
}
