.Timer {
  width: 100%;
  height: 100%;
}

.Timer .timer {
  width: 100%;
  height: 100%;
  border: none;
  background-color: white;
}

.progressbar {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0;
  transition: 1s;
  transition-timing-function: linear;
}

.display {
  position: absolute;
  left: calc(50% - 5rem);
  top: calc(50% - 5rem);
  width: 10rem;
  height: 10rem;
  font-size: 5rem;
  color: black;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tick {
  animation-name: tick;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
}

@keyframes tick {
  0%   {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}

.repeat {
  position: absolute;
  left: calc(50% - 1.5rem);
  top: calc(50% + 4rem);
  width: 3rem;
  height: 3rem;
  background-color: gainsboro;
  border: none;
  outline: none;
  border-radius: 50%;
  display: block;
  padding: 0.6rem;
  color: gainsboro;
  transition: 100ms;
}

.repeat:hover, .repeat:focus {
  box-shadow: 0 0 0.5rem black;
}

.repeat img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
