a {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  width: 3rem;
  height: 3rem;
  background-color: gainsboro;
  outline: none;
  border-radius: 50%;
  display: block;
  padding: 0.6rem;
  color: gainsboro;
  transition: 100ms;
}

a:hover, a:focus {
  box-shadow: 0 0 0.5rem black;
}

a img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
